import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders} from '@angular/common/http';

import { Houseinfo} from './houseinfo/HouseInfo';
import { UserService,houseState } from '../user.service';
import { fail } from 'assert';
import { Router } from '@angular/router';

declare var $:any;

class psdInfo{
  oldpsd:string;
  newpsd:string;
  check:string;
}

@Component({
  selector: 'app-personal',
  templateUrl: './personal.component.html',
  styleUrls: ['./personal.component.css']
})
export class PersonalComponent implements OnInit {

  u : string ;
  houseinfo : Houseinfo = { address : null, area:null ,price : null, houseName: null,message :null,type:null};
  arr: Array<String> = ["1","2","3","4","5","6","7","8","9"];
  room : string = "1";
  hall : string = "1";
  fileList: FileList ;


  index:boolean = true;
  collection:boolean = false;
  modifyPassword:boolean = false;

  //更改密码
  psdchange:psdInfo={oldpsd:null,newpsd:null,check:null};
  newcheck:Boolean=false;//更改成功判断
  oldcheck :Boolean = true; //判断是否旧密码出错
  registSuccess : Boolean = false; //判断房屋信息是否注册成功
  uploadPicture : Boolean = true;  //判断图片上传是否符合要求

  constructor(private http:HttpClient,private userService : UserService,private router:Router) {
    this.u = this.userService.acc;
   }

  ngOnInit() {

  }

  toIndex(){
    this.index = true; this.collection = false; this.modifyPassword = false;
  }

  toCollection(){
    this.index = false; this.collection = true; this.modifyPassword = false;
    this.userService.getUserHouse()//获取房东房屋列表
  }

  toModifyPassword(){
    this.index = false; this.collection = false; this.modifyPassword = true;
  }

  userDropHouse(s:string){
    this.userService.userDropHouse(s)
    this.userService.getUserHouse()
  }
  toDetail(s:string){ 
    this.router.navigate(['detail',s])
  }

  reset1(){
    this.newcheck = false;
    this.oldcheck = true;
    this.registSuccess = false;
  }

  fileChange(event){
    this.fileList = event.target.files;
    if(this.fileList.length > 8 || this.fileList.length < 1){
      this.uploadPicture = false;
    }else{
      this.uploadPicture = true;
    }

  }

    //改密码实现
    psdChange(){
      this.http.post<Boolean>('/house/Login/PsdChange',{oldp:this.psdchange.oldpsd,newp:this.psdchange.newpsd}).subscribe(
        data => {this.newcheck = data;this.oldcheck = data}
      );
    }

    //提交租房表单
    onSubmit(){
        this.houseinfo.type = this.room + "房" + this.hall + "厅";
        this.http.post<Boolean>('/house/Login/lendHouse',this.houseinfo).subscribe(data => this.registSuccess = !data);

        setTimeout(() => {
          this.registSuccess = !this.registSuccess;
          if(this.fileList.length > 0) {
            let formData:FormData = new FormData();
            for( let i = 0;i<this.fileList.length;i++){
              let file: File = this.fileList[i];
              formData.append('uploadFile', file, file.name);
            }
          let headers = new HttpHeaders();
          headers.append('Content-Type', 'multipart/form-data');
          headers.append('Accept', 'application/x-www-form-urlencoded');
          this.http.post('/house/Login/picture',formData,{headers : headers}).subscribe();
            
          }
        }, 1500);

        
    }

}



