import { Injectable } from '@angular/core';
import { User} from './nav/user/User';
import { HttpClient} from '@angular/common/http'

@Injectable()
export class UserService {

  constructor(private http : HttpClient) {}
  userHouse:Array<houseState>
  login : Boolean;
  acc : string;

  CheckLogin(){
    this.http.get<Boolean>('/house/Login/checkSession').subscribe(data => this.login = data);
  }
  loginout(){
    this.http.get<Boolean>('/house/Login/loginOut').subscribe(data => {this.login = false,this.acc=null;});
  }

  getUserHouse(){
    // this.userHouse=[{houseNum:"110",houseName:"emmm",state:1}]
    this.http.post<Array<houseState>>('/house/login/userHouse',null).subscribe(
      data => this.userHouse = data
    );
  }
  userDropHouse(s:string){
    this.http.post('/house/login/applyDrop',s).subscribe();
  }

}

export class houseState{
  houseNum:string
  houseName:string
  state:number
}


