import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router'

import { houseDetailMsg } from '../house-detail-message';
import { HouseFlowService } from '../house-flow.service'
import { AfterContentInit } from '@angular/core/src/metadata/lifecycle_hooks';

@Component({
  selector: 'house-detail',
  templateUrl: './house-detail.component.html',
  styleUrls: ['./house-detail.component.css']
})
export class HouseDetailComponent implements OnInit,AfterContentInit {
  id:string
  select_img:string
  constructor(private house:HouseFlowService,private active:ActivatedRoute) { 
   
  }

  ngOnInit() {

    this.id=this.active.snapshot.params['id']
    this.house.getHouseDeatilMsg(this.id)
    
    setInterval(()=>this.imgNext(),3000)
    
  
  }

  ngAfterContentInit(){
    this.select_img=this.house.msg.houseImg[0]
  }


  imgChange(s:string){
    this.select_img=s
  }
  imgNext(){
    var n=this.house.msg.houseImg.indexOf(this.select_img)
    if(n<this.house.msg.houseImg.length-1)
      this.select_img=this.house.msg.houseImg[n+1]
    else
      this.select_img=this.house.msg.houseImg[0]
  }
  imgPrev(){
    var n=this.house.msg.houseImg.indexOf(this.select_img)
    if(n>0)
      this.select_img=this.house.msg.houseImg[n-1]
    else
      this.select_img=this.house.msg.houseImg[this.house.msg.houseImg.length-1]
  }

}
 


