import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router,ActivatedRoute} from '@angular/router';
import { User} from '../user/User';
import { UserService} from '../../user.service';

class UserCheck {
  accCheck: boolean;
  psdCheck: boolean;
}

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  user: User = { acc: null, psd: null , realName:null, email:null};
  check: UserCheck = { accCheck: true, psdCheck: true };

  constructor(private http: HttpClient,private router : Router,private active : ActivatedRoute,private userService : UserService) { }

  ngOnInit() {
  }

  login() {
    this.http.post<UserCheck>('/house/Login/Check', this.user).subscribe(data => {
      this.check = data;
      if(data.accCheck == true && data.psdCheck == true){
          this.userService.acc = this.user.acc; 
          this.router.navigate([''],{relativeTo:this.active});
      }
    });
  }

  reset() {
    this.check.accCheck = true;
    this.check.psdCheck = true;
  }

}



