import { Component, OnInit } from '@angular/core';
import { ManagerService } from '../manager.service';
import { Router } from '@angular/router';

@Component({
  selector: 'drop-table',
  templateUrl: './drop-table.component.html',
  styleUrls: ['./drop-table.component.css']
})
export class DropTableComponent implements OnInit {

  constructor(private manager:ManagerService,private router:Router) { }

  ngOnInit() {
    this.manager.getDropHouse()
  }
  toDetail(s:string){ 
    this.router.navigate(['detail',s])
  }

}



