import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http'

@Injectable()
export class ManagerService {
  HOUSES:Array<house>
  constructor(private http : HttpClient) { }

  validate(s:userLogin){
    if(s.account=='admin'&&s.password=='admin')
      return true
    else
      return false
  }

  getHireHouse(){ 
    // var h:Array<house>=[
    //   {houseNum:'131',acc:'weker',tel:'110',manager:'xxx'},
    //   {houseNum:'233',acc:'mistborn',tel:'120',manager:'yyy'},
    //   {houseNum:'099',acc:'link',tel:'119',manager:'zzz'}
    // ]
    // this.HOUSES=h
    let getState = 2;
    this.http.post<Array<house>>('/house/admin/HireHouse',getState).subscribe(data => this.HOUSES = data)
  }

  getReviewHouse(){
    let getState = 1;
    this.http.post<Array<house>>('/house/admin/HireHouse',getState).subscribe(data => this.HOUSES = data)
  }

  getDropHouse(){
    let getState = 3;
    this.http.post<Array<house>>('/house/admin/HireHouse',getState).subscribe(data => this.HOUSES = data)
  }

  Drop(s:string,n:string){
    //已上架房屋下架和待下架房屋下架
    if(confirm('确定下架房屋'+s)){
      this.http.post("/house/admin/drop",s).subscribe(data => {
        if(n=='hire') this.getHireHouse()
        else if(n=='drop') this.getDropHouse()
      });
      
    }
  }
  Up(s:string,m:string){
    //房屋申请上架, 向后台传输房屋编号和处理的经纪人
    if(confirm('确定上架房屋'+s+'交由'+m+'处理')){
      this.http.post("/house/admin/up",{"houseNum":s,"manager":m}).subscribe(data => {
        this.getReviewHouse()
      });
    }
  }
  Back(s:string,n:string){
    //待下架或待上架房屋撤回
    if(n=='up'){
      if(confirm('确定撤回')){
        this.http.post("/house/admin/drop",s).subscribe(data => {
          this.getReviewHouse()
        });
      }
    }
    else if(n=='drop'){
      if(confirm('确定撤回')){
        this.http.post("/house/admin/cancelDrop",s).subscribe(data => {
          this.getDropHouse()
        });
    }
  }
  }

}


export class userLogin{
  account:string
  password:string
}

export class house{
  houseNum:string
  acc:string
  tel:string
  manager:string

}


