import { Component, OnInit, Input } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router'
import { houseSortMsg } from '../house-sort-message';


@Component({
  selector: 'house-sort',
  templateUrl: './house-sort.component.html',
  styleUrls: ['./house-sort.component.css']
})
export class HouseSortComponent implements OnInit {
  @Input() house:houseSortMsg
  constructor(private router:Router,private active:ActivatedRoute) { 
    
  }

  ngOnInit() {

  }
  toDetail(){
    this.router.navigate(['detail',this.house.houseNum],{relativeTo:this.active})
  }

}
 


