import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { ManagerService} from '../manager.service';

@Component({
  selector: 'hire-table',
  templateUrl: './hire-table.component.html',
  styleUrls: ['./hire-table.component.css']
})
export class HireTableComponent implements OnInit {

  constructor(private manager:ManagerService,private router:Router) { }

  ngOnInit() {
    this.manager.getHireHouse()
  }


  toDetail(s:string){ 
    this.router.navigate(['detail',s])
  }
}



