import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http'; 
import { Location} from '@angular/common'

import {User} from '../user/User';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  user:User={acc:null,psd:null,email:null,realName:null}
  psdcheck:string=null; //密码校验
  createSuccess:Boolean=false;
  newacc:string=null;
  createFail:Boolean=true;

  constructor(private http:HttpClient,private location :Location) { }

  ngOnInit() {
  } 

  sub(){
    this.http.post<Boolean>('/house/Register/Create',this.user).subscribe(data=>{
      this.createSuccess=data;
      this.createFail=data;
      if(data){
        this.newacc=this.user.acc;
      }
    });
  }

  goBack(){
    this.location.back();
  }
  reset0(){
    this.createSuccess = false;
    this.createFail = true;
  }

}



