import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms'
import { Routes,RouterModule} from '@angular/router'
import { HttpClientModule} from '@angular/common/http'


import { AppComponent } from './app.component';
import { NavComponent } from './nav/nav.component';
import { SearchComponent } from './search/search.component';
import { HouseListComponent } from './house-list/house-list.component';
import { FilterComponent } from './filter/filter.component';
import { CollectionComponent } from './collection/collection.component';

import { HouseFlowService } from "./house-flow.service";
import { HouseSortComponent } from './house-sort/house-sort.component';
import { HouseDetailComponent } from './house-detail/house-detail.component';

import { LoginComponent } from './nav/login/login.component';
import { RegisterComponent } from './nav/register/register.component';
import { PersonalComponent } from './personal/personal.component';
import { UserService} from './user.service';
import { AdminLoginComponent } from './admin-login/admin-login.component';
import { ManagerService } from './manager.service';
import { ManagerComponent } from './manager/manager.component';
import { HireTableComponent } from './hire-table/hire-table.component';
import { ReviewTableComponent } from './review-table/review-table.component';
import { DropTableComponent } from './drop-table/drop-table.component';

const routes:Routes=[
  {path:'',component:HouseListComponent},
  {path:'test',component:HouseDetailComponent},
  {path:'detail/:id',component:HouseDetailComponent},
  {path:"login",component:LoginComponent},
  {path:"register",component:RegisterComponent},
  {path:"personal",component:PersonalComponent},
  {path:'admin',component:AdminLoginComponent},
  {path:'manager',component:ManagerComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    SearchComponent,
    HouseListComponent,
    FilterComponent,
    CollectionComponent,
    HouseSortComponent,
    HouseDetailComponent,
    LoginComponent,
    RegisterComponent,
    PersonalComponent,
    AdminLoginComponent,
    ManagerComponent,
    HireTableComponent,
    ReviewTableComponent,
    DropTableComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(routes,{useHash: true}),
    HttpClientModule
  ],
  providers: [HouseFlowService,UserService,ManagerService],
  bootstrap: [AppComponent]
})
export class AppModule { }



